.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}
.col,
[class*="col-"]{
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
}
.col{
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
}

.col-auto {
    width: auto;
    max-width: 100%;
}